/* eslint-disable */

export const getCookie = (name) => {
    const matches = document.cookie.match(new RegExp(
        "(?:^|; )" + name.replace(/([\.$?*|{}\(\)\[\]\\\/\+^])/g, '\\$1') + "=([^;]*)"
    ));
    return matches ? decodeURIComponent(matches[1]) : undefined;
}

export const setCookie = (name, value, options) => {
    options = options || {};

    var expires = options.expires;

    if (typeof expires == "number" && expires) {
        var d = new Date();
        d.setTime(d.getTime() + expires * 1000);
        expires = options.expires = d;
    }
    if (expires && expires.toUTCString) {
        options.expires = expires.toUTCString();
    }

    value = encodeURIComponent(value);

    var updatedCookie = name + "=" + value;

    for (var propName in options) {
        updatedCookie += "; " + propName;
        var propValue = options[propName];
        if (propValue !== true) {
            updatedCookie += "=" + propValue;
        }
    }

    document.cookie = updatedCookie;
}

export const setCookieDays = (name, value, day = 1) => {
    let dateCookie = new Date;
    dateCookie.setDate(dateCookie.getDate() + day);

    setCookie(name, value, { expires: dateCookie.toUTCString() })
}

export const setCookieMonth = (name, value) => {
    let dateCookie = new Date;
    dateCookie.setDate(dateCookie.getDate() + 31);

    setCookie(name, value, { expires: dateCookie.toUTCString(), path: "/" })
}

export const clearCookie = (name) => {
    setCookie(name, "", { expires: -1 });
}

