export default async function () {
	try {
		const response = await getApi('/api-k3/v3/profile/init');
		if (response.data.status === 'ok' && response.data.data) {
			store.commit('SET_USER', response.data.data);
			store.commit('SET_PROMOS', response.data.data.available_promos);

			if (response.data.data.contract) {
				store.commit('HAS_ACTIVE_CONTRACT', response.data.data.contract);
			}
		}

		console.log('user: ', response);
	} catch (error) {
		console.log(error);
		if (error.data?.status === 'error' && error.data.api_code === 99) {
			store.commit('LOGOUT');
		}
	}
}

