import isMobile from '@/shared/store/isMobile';
import isTablet from '@/shared/store/isTablet';
import mobileNav from '@/shared/store/mobileNav';
import esiaLogin from '@/features/AuthByEsia/store/esiaLogin';
import authByPhone from '@/features/AuthByPhone/store/authByPhone';
import historyFeatures from '@/features/HistoryFeatures/store/historyFeatures';
import addresses from '@/features/FetchAddresses/store/addresses';
import { clearCookie, getCookie } from '@/shared/lib/helpers/cookies';

const {
	VUE_APP_VERSION,
	VUE_APP_HOST,
	VUE_APP_FILEUIDLIST_WORKCERTIFICATE,
	VUE_APP_FILEUIDLIST_PHOTOWITHPASSPORT,
	VUE_APP_FILEUIDLIST_PHOTO1STAND2NDPAGESPASSPORT,
	VUE_APP_FILEUIDLIST_PHOTOPASSPORTWITHMARITALSTATUS,
	VUE_APP_FILEUIDLIST_PHOTOPASSPORTWITHREGISTRATION,
	VUE_APP_FILEUIDLIST_PHOTOPASSPORTWITHPREVPASSPORTS,
	VUE_APP_UUIDSITE,
	VUE_APP_GLOBALDEVMODE,
	VUE_APP_SITEBACKURL,
	VUE_APP_WSADDRESS,
} = process.env;
console.log('store process.env: ', process.env);

export default createStore({
	state: {
		token: getCookie('token'),
		centrifugeToken: getCookie('centrifugeToken'),
		WSAddress: VUE_APP_WSADDRESS,
		user: null,
		availablePromos: [],
		contract: null,
		hasActiveContract: false,
		paymentMethod: 'bank',
		showPaymentMethods: false,
		globalPreloader: false,
		buttonPreloader: false,
		host: VUE_APP_HOST,
		version: VUE_APP_VERSION || '0',
		devmode: (VUE_APP_GLOBALDEVMODE === 'true'),
		fileUIDList: {
			work_certificate: VUE_APP_FILEUIDLIST_WORKCERTIFICATE,
			photo_with_passport: VUE_APP_FILEUIDLIST_PHOTOWITHPASSPORT,
			photo_1st_and_2nd_pages_passport: VUE_APP_FILEUIDLIST_PHOTO1STAND2NDPAGESPASSPORT,
			photo_passpowrt_with_marital_status: VUE_APP_FILEUIDLIST_PHOTOPASSPORTWITHMARITALSTATUS,
			photo_passpowrt_with_registration: VUE_APP_FILEUIDLIST_PHOTOPASSPORTWITHREGISTRATION,
			photo_passpowrt_with_prev_passports: VUE_APP_FILEUIDLIST_PHOTOPASSPORTWITHPREVPASSPORTS,
		},
		siteBackUrl: VUE_APP_SITEBACKURL,
		uuidSite: VUE_APP_UUIDSITE,
	},
	mutations: {
		SET_GLOBAL_PRELOADER(state, val) {
			state.globalPreloader = val;
		},
		SET_BUTTON_PRELOADER(state, val) {
			state.buttonPreloader = val;
		},
		SET_TOKEN(state, val) {
			state.token = val;
		},
		SET_CENTRIFUGE_TOKEN(state, val) {
			state.centrifugeToken = val;
		},
		SET_USER(state, val) {
			state.user = val;
		},
		SET_PROMOS(state, val) {
			state.availablePromos = val;
		},
		SET_ACTIVE_CONTRACT(state, val) {
			state.contract = val;
		},
		HAS_ACTIVE_CONTRACT(state, val) {
			state.hasActiveContract = val;
		},
		SET_PAYMENT_METHOD(state, val) {
			state.paymentMethod = val;
		},
		SHOW_PAYMENT_METHODS(state, val) {
			state.showPaymentMethods = val;
		},
		LOGOUT(state) {
			clearCookie('token');
			clearCookie('centrifugeToken');
			state.token = null;
			state.centrifugeToken = null;
			window.location.href = '/login';
		},
	},
	actions: {

	},
	modules: {
		// auth,
		// logs,
		// centrifuge,
		// user,
		// loanApp,
		// tariff,
		// payout,
		// contract,
		// grayAreaHeight,
		isMobile,
		isTablet,
		mobileNav,
		esiaLogin,
		authByPhone,
		historyFeatures,
		addresses,
		// requestCall,
	},
});
