const SharedPage = () => import(/* webpackChunkName: "shared-page" */'@/pages/SharedPage');
const NotFound = () => import(/* webpackChunkName: "NotFound" */'@/pages/NotFound');

export const routes = [
	{
		path: '/',
		name: 'home',
		component: Home,
		// meta: {	middleware: [hasContract] },
		beforeEnter: (to, from, next) => {
			if (!store.state.token) {
				return next({ name: 'login' });
			}
			if (store.state.hasActiveContract && store.state.token) {
				return next({ name: 'main-page' });
			}
			return next();
		},
	},
	{
		path: '/shared',
		name: 'shared',
		component: SharedPage,
		meta: { middleware: [isAuth] },
	},
	{
		path: '/archieve',
		name: 'archieve',
		component: Archieve,
		meta: { middleware: [isAuth] },
	},
	{
		path: '/addresses',
		name: 'addresses',
		component: Addresses,
		meta: { middleware: [isAuth] },
	},
	{
		path: '/login',
		name: 'login',
		component: Login,
		meta: { middleware: [noContract] },
	},
	{
		path: '/loan-app',
		name: 'loan-app',
		component: LoanApplication,
		meta: { middleware: [isAuth, hasLoanApp] },
	},
	// { path: '/loan-docs', name: 'loan-docs', component: LoanDocs },
	// { path: '/loan-inprogress', name: 'loan-inprogress', component: LoanInProgress },
	// { path: '/approved', name: 'approved', component: LoanAppApproved },
	// { path: '/getmoney', name: 'getmoney', component: LoanAppGetMoney },
	// { path: '/getmoney-docs', name: 'getmoney-docs', component: LoanAppGetMoneyDocs },
	{
		path: '/sendmoney',
		name: 'sendmoney',
		component: SendMoney,
		meta: { middleware: [isAuth] },
	},
	// { path: '/rework', name: 'rework', component: ReworkToClient },
	{
		path: '/contract',
		name: 'contract',
		component: Contract,
		meta: { middleware: [hasContract] },
	},
	{
		path: '/contract-info',
		name: 'contract-info',
		component: ContractInfo,
		meta: { middleware: [hasContract] },
	},
	{
		path: '/contract-docs',
		name: 'contract-docs',
		component: ContractDocs,
		meta: { middleware: [hasContract] },
	},
	{
		path: '/payment',
		name: 'payment',
		component: Payment,
		meta: { middleware: [isAuth] },
	},
	{
		path: '/main-page',
		name: 'main-page',
		component: MainPage,
		meta: { middleware: [hasContract] },
	},
	{
		path: '/payment-history',
		name: 'payment-history',
		component: PaymentHistory,
		meta: { middleware: [isAuth] },
	},
	{
		path: '/qr-code-payment',
		name: 'qr-code-payment',
		component: QrCodePayment,
		meta: { middleware: [isAuth] },
	},
	// { path: '/cardpay', name: 'cardpay', component: CardPay },
	// { path: '/payment/:id/fail', name: 'payment-fail', component: PaymentFail },
	// { path: '/payment/:id/done', name: 'payment-done', component: PaymentDone },
	// { path: '/contract-docs', name: 'contract-docs', component: ContractDocs },
	// { path: '/contract-details', name: 'contract-details', component: ContractDetails },
	{ path: '/:pathMatch(.*)*', name: 'page-not-found', component: NotFound },
];

export const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes,
	linkActiveClass: 'active',
});

router.afterEach(async () => {
	console.log('router.currentRoute.value: ', router.currentRoute.value);
	if (store.state.user && router.currentRoute.value.name !== 'addresses') {
		if (store.state.user.contract && !store.state.contract) {
			await getContract();
		}
	}
	console.log('AFTER EACH');
	console.log(store.state.user);
	console.log(store.state.contract);
});
router.beforeEach(async (to, from, next) => {
	if (store.state.token && !store.state.user) {
		await initUser();
		console.log('BEFORE EACH');

		// if (store.state.centrifugeToken) {
		// 	await initCentrifuge();
		// }
	}

	if (!to.meta.middleware) {
		return next();
	}
	const { middleware } = to.meta;
	const context = {
		to,
		from,
		next,
		store,
		router,
	};

	return middleware[0]({
		...context,
		next: middlewarePipeline(context, middleware, 1),
	});
});
