import { getApi } from '../../../shared/api';

export default {
	namespaced: true,
	state: () => ({
		isLoading: false,
		items: {},
	}),
	mutations: {
		setHistoryItems(state, val) {
			state.items = val;
		},
		setLoading(state, val) {
			state.isLoading = val;
		},
	},
	actions: {
		// eslint-disable-next-line no-empty-pattern
		async getHistoryItemsAction({ commit }, options) {
			let paramStatuses = '';
			options.statuses.forEach((item) => {
				paramStatuses += `statuses[]=${item}&`;
			});
			try {
				const res = await getApi(`/api-k3/v3/profile/history-operations?${paramStatuses}limit=${options.limit}&contract_uid=${options.contract_uid}`);
				if (res?.data?.data) {
					// console.log('RES: ', res);
					const list = {};
					res.data.data.forEach((item) => {
						if (list[item.date.slice(0, 10)]) {
							list[item.date.slice(0, 10)].push(item);
						} else {
							list[item.date.slice(0, 10)] = [item];
						}

						// if (list[item.date.slice(0, 10)]) {
						// 	if (list[item.date]) {
						// 		list[item.date].push(item);
						// 	}
						// } else {
						// 	list[item.date.slice(0, 10)] = [item];
						// }
					});
					commit('setHistoryItems', list);
				}
			} catch (e) {
				console.log(e);
			}
		},
	},
};
