import Axios from 'axios';
import { getCookie } from '@/shared/lib/helpers/cookies';

const headers = {
	'Content-Type': 'application/json',
	// 'Authorization': `Bearer ${getCookie('tokenOperator')}`
	Authorization: `${getCookie('token')}`,
};

const api = async (method, path, data, responseType) => {
	const options = {
		method,
		url: path,
		headers,
	};
	// eslint-disable-next-line no-unused-expressions
	method === 'post' || method === 'put' ? options.data = data || {} : '';
	if (method === 'get') {
		options.data = {};
	}
	if (responseType) {
		options.responseType = responseType;
	}
	// eslint-disable-next-line no-useless-catch
	try {
		return await Axios(options);
	} catch (e) {
		throw e.response;
	}
};

export const getApi = (path, responseType) => api('get', path, {}, responseType);
export const deleteApi = (path) => api('delete', path);
export const postApi = (path, jsonData) => api('post', path, jsonData);
export const putApi = (path, jsonData) => api('put', path, jsonData);
