import axios from 'axios';

export default {
	namespaced: true,
	state: () => ({}),
	actions: {
		// eslint-disable-next-line camelcase
		async sendPhoneAndBirthday(_, { phone, birthday }) {
			const response = await axios.post('/api-k3/v2/login/request', {
				birthday,
				phone,
				// birth_date: '1986-06-12',
				// phone: '9279484143',

				// birth_date: '1997-01-29',
				// phone: '9968931508'
			});

			return response;
		},
		// eslint-disable-next-line camelcase
		async sendCode(_, { phone, birthday, sms_code }) {
			const response = await axios.post('/api-k3/v2/login', {
				birthday,
				phone,
				sms_code,

				// birth_date: '1986-06-12',
				// phone: '9279484143',
				// code: '0000'

				// birth_date: '1997-01-29',
				// phone: '9968931508',
				// code
			});

			return response;
		},
	},

};
