import { getApi } from '@/shared/api';

export default {
	namespaced: true,
	state: () => ({
		addresses: [],
		initialCity: 'Уфа',
		showCitiesList: false,
	}),
	mutations: {
		setAddresses(state, val) {
			state.addresses = val;
		},
		SHOW_CITIES_LIST(state, val) {
			state.showCitiesList = val;
		},
		SET_CITY(state, val) {
			state.initialCity = val;
		},
	},
	actions: {
		async getAddresses({ commit }) {
			try {
				const res = await getApi('/api-k3/v3/office');
				if (res?.data?.data && res.data.status === 'ok') {
					commit('setAddresses', res.data.data);
					console.log(res);
				}
			} catch (e) {
				console.log(e);
			}
		},
		// eslint-disable-next-line no-empty-pattern
		async getOfficeImages({}, { picture }) {
			let result;

			try {
				const res = await getApi(`/api-k3/v3/office/${picture}/`, 'blob');
				console.log('picture: ', res);
				result = window.URL.createObjectURL(res.data);
			} catch (e) {
				console.log(e);
			}

			return result;
		},
	},
	getters: {
		getCurrentCityOffices(state) {
			return state.addresses.filter((office) => office.address_data.data.serialize.find((item) => item.name === state.initialCity));
		},
		getOfficeCities(state) {
			const mappedAddresses = state.addresses?.map((data) => {
				const obj = {};

				const RegionAndCityData = data.address_data.data.serialize;

				RegionAndCityData.forEach((item) => {
					if (item.level === 'Region') {
						obj.type = item.type;
						obj.name = item.name;
					}
					if ((item.level === 'City' || item.level === 'Place') && item.name) {
						obj.city = item.name;
					}
				});

				return obj;
			});

			const arr = [];
			mappedAddresses.forEach((item) => {
				arr[item.city] = item;
			});

			return Object.values(arr);
		},
	},
};
